
.row{
    display: flex;
    justify-content:space-between;
    flex-wrap: wrap;
    /* border:1px solid red; */
}
.contact_left{
    flex-basis:35%;
}
.contact_right{
    flex-basis:60%;
}
.contact_left p{
   margin-top:30px; 
}
.contact_left p i{
    color:#ff004f;
    margin-right: 15px;
    font-size:25px;
}
.social_icons a{
    text-decoration: none;
    font-size:30px;
    margin-right:15px;
    color:#ababab;
    display:inline-block;
    transition: transform 0.5s;
}
.social_icons a:hover{
    background-color:#ff004f;
    transform:translateY(-5px);
}
.btn{
    display: block;
    margin:50px auto;
    width:fit-content;
    border:1px solid #ff004f;
    padding:14px 50px;
    border-radius: 6px;
    text-decoration:none;
    color:#fff;
    transition:background 0.5s;
    /* here transition is added to background */
}
.btn:hover{
    background:#ff004f;
}
.btn.btn2{
    display:inline-block;
    background:#ff004f;
}
.contact_right form{
    width:100%;
}
form input, form textarea{
    width:100%;
    border:0;
    outline:none;
    background:#262626;
    padding:15px;
    margin:15px 0;
    color:#fff;
    font-size:18px;
    border-radius: 6px;
}
form .btn2{
    padding:14px 60px;
    font-size:18px;
    margin-top:20px;
    cursor:pointer
}
.copyright{
    width:100%;
    text-align: center;
    padding:25px 0;
    background:#262626;
    font-weight: 300;
    margin-top: 20px;
}
.copyright svg{
   color:#ff004f; 
}
@media only screen and (max-width:600px){
    .contact_left,.contact_right{
        flex-basis:100%;
    } 
    .copyright{
        font-size:14px;
    }
}
.msg{
    color:#61b752;
    margin-top:-40px;
    display:block;
}